import extend from '../functions/extend';
import { globalOptions } from './utils/ajax';
/**
 *  Ajax 
 * @param options 
 * @example
```js
ajaxSetup({
  dataType: 'json',
  method: 'POST',
});
```
 */
function ajaxSetup(options) {
    return extend(globalOptions, options);
}
export default ajaxSetup;
