import $ from '../$';
import data from '../functions/data';
import { isObjectLike, isString, isUndefined, toCamelCase, toKebabCase, } from '../utils';
import './each';
const rbrace = /^(?:{[\w\W]*\}|\[[\w\W]*\])$/;
//  `data-*` 
function getData(value) {
    if (value === 'true') {
        return true;
    }
    if (value === 'false') {
        return false;
    }
    if (value === 'null') {
        return null;
    }
    if (value === +value + '') {
        return +value;
    }
    if (rbrace.test(value)) {
        return JSON.parse(value);
    }
    return value;
}
//  value  `data-*` 
function dataAttr(element, key, value) {
    if (isUndefined(value) && element.nodeType === 1) {
        const name = 'data-' + toKebabCase(key);
        value = element.getAttribute(name);
        if (isString(value)) {
            try {
                value = getData(value);
            }
            catch (e) { }
        }
        else {
            value = undefined;
        }
    }
    return value;
}
$.fn.data = function (key, value) {
    // 
    if (isUndefined(key)) {
        if (!this.length) {
            return undefined;
        }
        const element = this[0];
        const resultData = data(element);
        // window, document  `data-*` 
        if (element.nodeType !== 1) {
            return resultData;
        }
        //  `data-*` 
        const attrs = element.attributes;
        let i = attrs.length;
        while (i--) {
            if (attrs[i]) {
                let name = attrs[i].name;
                if (name.indexOf('data-') === 0) {
                    name = toCamelCase(name.slice(5));
                    resultData[name] = dataAttr(element, name, resultData[name]);
                }
            }
        }
        return resultData;
    }
    // 
    if (isObjectLike(key)) {
        return this.each(function () {
            data(this, key);
        });
    }
    // value  undefined
    if (arguments.length === 2 && isUndefined(value)) {
        return this;
    }
    // 
    if (!isUndefined(value)) {
        return this.each(function () {
            data(this, key, value);
        });
    }
    // 
    if (!this.length) {
        return undefined;
    }
    return dataAttr(this[0], key, data(this[0], key));
};
