import extend from 'mdui.jq/es/functions/extend';
import { isFunction, isUndefined } from 'mdui.jq/es/utils';
import mdui from '../../mdui';
import { Dialog } from './class';
import './dialog';

declare module '../../interfaces/MduiStatic' {
  interface MduiStatic {
    /**
     * 
     * @param text 
     * @param title 
     * @param onConfirm 
     * @param onCancel 
     * @param options 
     */
    confirm(
      text: string,
      title: string,
      onConfirm?: (dialog: Dialog) => void,
      onCancel?: (dialog: Dialog) => void,
      options?: OPTIONS,
    ): Dialog;

    /**
     * 
     * @param text 
     * @param onConfirm 
     * @param onCancel 
     * @param options 
     */
    confirm(
      text: string,
      onConfirm?: (dialog: Dialog) => void,
      onCancel?: (dialog: Dialog) => void,
      options?: OPTIONS,
    ): Dialog;
  }
}

type OPTIONS = {
  /**
   * 
   */
  confirmText?: string;

  /**
   * 
   */
  cancelText?: string;

  /**
   *  hashchange  `true`  Android  `true`
   */
  history?: boolean;

  /**
   *  `false`  `false`
   */
  modal?: boolean;

  /**
   *  Esc  `true`
   */
  closeOnEsc?: boolean;

  /**
   * 
   */
  closeOnCancel?: boolean;

  /**
   * 
   */
  closeOnConfirm?: boolean;
};

const DEFAULT_OPTIONS: OPTIONS = {
  confirmText: 'ok',
  cancelText: 'cancel',
  history: true,
  modal: false,
  closeOnEsc: true,
  closeOnCancel: true,
  closeOnConfirm: true,
};

mdui.confirm = function (
  text: string,
  title?: any,
  onConfirm?: any,
  onCancel?: any,
  options?: any,
): Dialog {
  if (isFunction(title)) {
    options = onCancel;
    onCancel = onConfirm;
    onConfirm = title;
    title = '';
  }

  if (isUndefined(onConfirm)) {
    // eslint-disable-next-line @typescript-eslint/no-empty-function
    onConfirm = (): void => {};
  }

  if (isUndefined(onCancel)) {
    // eslint-disable-next-line @typescript-eslint/no-empty-function
    onCancel = (): void => {};
  }

  if (isUndefined(options)) {
    options = {};
  }

  options = extend({}, DEFAULT_OPTIONS, options);

  return mdui.dialog({
    title: title,
    content: text,
    buttons: [
      {
        text: options.cancelText,
        bold: false,
        close: options.closeOnCancel,
        onClick: onCancel,
      },
      {
        text: options.confirmText,
        bold: false,
        close: options.closeOnConfirm,
        onClick: onConfirm,
      },
    ],
    cssClass: 'mdui-dialog-confirm',
    history: options.history,
    modal: options.modal,
    closeOnEsc: options.closeOnEsc,
  });
};
